import React, { useEffect, useState } from "react";
import { useHistory } from "react-router";
import { useParams } from "react-router-dom";
import { SwiperSlide } from "swiper/react";
import facebook from '../../assets/facebook-yellow.png';
import instagram from '../../assets/instagram-yellow.png';
import { useAuth } from "../../auth/auth";
import app from "../../auth/config";
import Card from "../../components/Card";
//share
import Share from '../../components/CardCompartilhar/index';
import HorizontalList from "../../components/HorizontalList";
import Section from "../../components/Section";
import { useShows } from "../../dados/context/ShowsContext";
import Layout from '../../layout';
import * as S from "./styles";




function FichaCanal(props) {

  const { showLike, emAltaList, getAllChannels, channel, channelLike } = useShows();
  const { userIsAuthenticated } = useAuth();
  const userId = userIsAuthenticated() ? app.auth().currentUser.uid: '';
  const history = useHistory()
  const [compartilharFica, setcompartilharFicha] = useState(false);
  const [compartilhar, setcompartilhar] = useState(false);

  //pegando canaiss pela url
  const params = useParams('/FichaCanal/:id')
  const { id } = params

  useEffect(
    () => {
      getAllChannels({id})
    }, [id])

  return (
    <Layout>
      {(
        <>

            <>

              <S.Container>
                <S.ContainerImg>

                  <S.Thumbnail src={channel?.thumbnailGrande} ></S.Thumbnail>


                </S.ContainerImg>

                <S.Conteudo>
                  <S.ButtonVoltar onClick={() => history.push("/canais") || window.location.reload()}>
                    {" "}
                    <i className="bi bi-x-lg"></i>{" "}
                  </S.ButtonVoltar>

                  <S.HeaderConteudo>


                    <S.Item >
                    {userIsAuthenticated() && (
                      <S.Coracao
                      onClick={() => channelLike(userId,channel.id)}
                      >
                        <i className="bi bi-suit-heart"></i>
                      </S.Coracao>
                    )}

                      <S.Compartilhar >



                        {" "}

                        <i className="bi bi-share-fill" onClick={() => setcompartilharFicha(true)} style={{cursor:"pointer"}}></i>
                        {compartilharFica && <Share link={`http://www.palcoweb.com.br/FichaCanal/${channel?.id}`} sairficha={setcompartilharFicha}></Share>}
                      </S.Compartilhar>


                    </S.Item>
                    <S.Img
                      src={channel?.thumbnail}
                      style={{
                        margin: 0,
                        width: 150,
                        height: 150,
                        borderRadius: "50%",
                        alignItems: 'center',
                        lineHeight: 150
                      }}
                      value={channel?.key}
                      alt="imagem canal"
                    />
                    <S.Item>
                      <S.HeaderTitle> <S.Span size="1">{channel?.fullName}</S.Span></S.HeaderTitle>
                      {channel.socialNetwork && (
                        <>
                      <div>
                        {/*redes sociais */}
                        {channel?.socialNetwork?.facebook && <a href={channel?.socialNetwork?.facebook} target="_blank" rel="noopener noreferrer" style={{color:'#fdcc12',textDecorationLine:'none'}}> <img src={facebook} alt="logo do facebook" /></a>}
                        {channel?.socialNetwork?.instagram && <a href={'https://www.instagram.com/'+channel?.socialNetwork?.instagram} target="_blank" rel="noopener noreferrer"  style={{color:'#fdcc12',textDecorationLine:'none'}}> <img src={instagram} alt="logo do instagram" /></a>}
                      </div>
                        {channel?.socialNetwork?.site && <S.Span cor="#fdcc12" size="1" style={{ cursor: "pointer" }}> <a href={channel?.socialNetwork?.site} target="_blank" rel="noopener noreferrer" style={{color:'#fdcc12',textDecorationLine:'none'}}>Site Oficial</a> </S.Span>}
                        </>
                      )}

                    </S.Item>


                  </S.HeaderConteudo>


                  <S.Informacoes>



                    <S.Item>
                      <S.Text size="0.8"> {channel?.description || 'Descrição ainda indefinida'}</S.Text>
                    </S.Item>
                    <S.Item>
                      <S.Span size="0.8"><i className="bi bi-circle-fill p-1"></i>  {channel?.category || '-'}</S.Span>
                    </S.Item>



                  </S.Informacoes>
                </S.Conteudo>
              </S.Container>
              <Section title="" filter>
              <HorizontalList id="emAlta">
              {emAltaList?.map(show=>(
                    <Section key={show.id} title={show.name} filter>
                        <HorizontalList id={show.name}>

                      {show.spectacle && show.spectacle.length > 0 && show.spectacle.map(
                        (card)=>{
                          if (card.producer_id === channel?.id) {
                            return (
                          <SwiperSlide key={String(card.id)}>
                          {card === "" ? (
                            <Card noContent />
                          ) : (
                            <S.CardTest>
                              <S.Img
                                src={card.thumbnail}
                                style={{
                                  position: "absolute",
                                  width: ""
                                }}
                                value={card.id}
                                alt="imagem referente a peça"
                              />

                              <S.InfoCard>
                                <S.InfoCardConteudo>
                                  <S.Classificacao>
                                    {card.ageRating}
                                  </S.Classificacao>
                                  {userIsAuthenticated()&&
                                  (<S.Coracao
                                    onClick={() => {
                                      showLike(userId, card.id);
                                    }}
                                  >
                                    <i className="bi bi-suit-heart-fill"></i>
                                  </S.Coracao>)
                                  }


                                  <S.Compartilhar>
                                    <i
                                      className="bi bi-share-fill"
                                      onMouseEnter={() =>
                                        setcompartilhar(true)
                                      }
                                    ></i>
                                    {compartilhar && (
                                      <Share
                                        sair={setcompartilhar}
                                        link={`https://www.palcoweb.com.br/#/cardezao/${card.id}`}
                                      ></Share>
                                    )}
                                  </S.Compartilhar>
                                </S.InfoCardConteudo>

                                <S.Titulo>{card.title}</S.Titulo>
                                <S.ConteudoCard
                                  style={{
                                    textTransform: "uppercase",
                                  }}
                                >
                                  {show.name}
                                </S.ConteudoCard>
                                <S.Sinopsia>{card.synopsis}</S.Sinopsia>

                                <S.ButtonAcessar
                                  className="btn btn-warning"
                                  onClick={() => {
                                    history.push(`cardezao/${card.id}`);
                                    // window.location.reload();
                                  }}
                                >
                                  Saiba Mais
                                </S.ButtonAcessar>
                              </S.InfoCard>
                            </S.CardTest>
                          )}
                        </SwiperSlide>
                            )}else{
                              return (
                                <SwiperSlide>
                                <Card noContent />
                                </SwiperSlide>
                              )
                            }
                        }
                      )
                      }
                      </HorizontalList>
                    </Section>
                  ))}
                  </HorizontalList>
                </Section>
              {/*}
                    <Section title="Loja" filter>
                        <HorizontalList id="canais">
                            {ListaTeste.map((card, index) => {
                                return (
                                    <SwiperSlide key={String(index)}>
                                        {card === "" ? (
                                            <Card noContent />
                                        ) : (
                                            <S.Card >
                                                <S.Img src={'https://images.pexels.com/photos/7562644/pexels-photo-7562644.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940'} style={{ position: "absolute", width: 250, height: 250, }} value={card.key} />
                                            </S.Card>
                                        )}
                                    </SwiperSlide>
                                );
                            })}
                        </HorizontalList>
                    </Section>
                        {*/}

              {/* <S.Produtora>
                <S.ImgProdutora
                  src={require("../../assets/logo-chaim.png")}
                ></S.ImgProdutora>
                <S.Item>
                  Chaim Entretenimento <br /> A Chaim Entretenimento é
                  responsável por promo- ver entretenimento de alta qualidade
                  e proporcio- nar ao público grandes espetáculos teatrais e
                  musi- cais. Com 20 anos de experiência, produzindo e
                  promovendo diversos espetáculos por todo o Brasil.
                </S.Item>
                <S.Item>CONHEÇA O CANAL </S.Item>
                <S.Item className="m-1">
                  {" "}
                  <i className="bi bi-chevron-right"></i>{" "}
                </S.Item>
              </S.Produtora> */}

            </>
        </>
      )}
    </Layout>
  );
}

export default FichaCanal;
