import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { MdShoppingCart, MdMenu } from "react-icons/md";
import { FaUserCircle } from "react-icons/fa";
import * as S from "./styles";
import logo from "../../assets/logoPalcoweb.png";
import Button from "../Button";
import { useHistory } from "react-router-dom";
import firebase from "firebase"
import { useAuth } from "../../auth/auth";

function testIsDesktop() {
  if (typeof window === "undefined") {
    return true;
  }
  return window.innerWidth >= 768;
}

function Header({ page }) {
  const {userSigned, userIsAuthenticated, signOut, signature} = useAuth();
  const [select, setSelect] = useState(page);
  const [toggle, setToggle] = useState(false);
  const pages = ["Início", "Canais", "Saiba Mais", "Categorias", "TV PalcoWeb"];
  const pagesToggle = [
    "Início",
    "Canais",
    "Saiba Mais",
    "Categorias",
    // "TV PalcoWeb",
    "Entrar",
    "Assine já",
    "Carrinho de Compras",
  ];
  const pagesToggleLogado = [
    "Início",
    "Assinatura",
    "Canais",
    "Categorias",
    "Saiba Mais",
    "Carrinho de Compras",
    "Meus Espetáculos",
    "Minha Conta",
    "",
    // "TV PalcoWeb",
    "Sair",
  ];
  console.log('Usuario', userSigned);

  const history = useHistory();

  const pathFunction = (index) => {
    switch (index) {
      case 0:
        return "/";
      case 1:
        return "/canais";
      case 2:
        return "/SaibaMais";
      case 3:
        return "/categorias";
      // case 4:
      //   return "/canalpw";
      case 4:
        return "/login";
      case 5:
        return "/Assinatura";
      case 6:
        return "/cart";
      default:
        return "/";
    }
  };

  const pathFunctionLogado = (index) => {
    switch (index) {
      case 0:
        return "/";
      case 1:
        return "/espetaculos/assinante";
      case 3:
        return "/categorias";
      case 2:
        return "/canais";
      case 4:
        return "/SaibaMais";
      case 5:
        return "/cart";
      case 6:
        return "/espetaculos";
      case 7:
        return "/perfil";
      case 9:
        return "/canalpw";
      default:
        return "/";
    }
  };

  const [perfilButton, setperfilButton] = useState(false);

  if (userIsAuthenticated()) {
    return (
      <>
        {toggle && (
          <S.ModalMenu>
            {userIsAuthenticated()
              ? pagesToggleLogado.map((page, index) => {
                  if (page === "Sair") {
                    return (
                      <S.PageToggle
                        key={String(page)}
                        select={Number(index) === Number(select)}
                        onClick={() => {
                          signOut();
                          setSelect(index);
                        }}
                      >
                        <Link to={pathFunctionLogado(index)}>{page}</Link>
                      </S.PageToggle>
                    );
                  }
                  if (page === "Saiba Mais") {
                    return (
                      <S.PageToggle
                        key={String(page)}
                        select={Number(index) === Number(select)}
                        onClick={() => {
                          history.push("/SaibaMais")
                        }}
                      >
                        <Link>{page}</Link>
                      </S.PageToggle>
                    );
                  }
                  return (
                    <S.PageToggle
                      key={String(page)}
                      select={Number(index) === Number(select)}
                      onClick={() => setSelect(index)}
                    >
                      <Link to={pathFunctionLogado(index)}>{page}</Link>
                    </S.PageToggle>
                  );
                })
              : pagesToggle.map((page, index) => {
                  return (
                    <S.PageToggle
                      key={String(page)}
                      select={Number(index) === Number(select)}
                      onClick={() => setSelect(index)}
                    >
                      <Link to={pathFunction(index)}>{page}</Link>
                    </S.PageToggle>
                  );
                })}
          </S.ModalMenu>
        )}
        <S.Container>
          <S.Content>
            <img src={logo} alt="Logotipo Palcoweb" onClick={() => history.push("/")} />
            <S.Buttoni
            onClick={() => history.push(`/canalpw`)}
            >
              TV Palcoweb
            </S.Buttoni>
            <S.Pages>
              {pages.map((page, index) => {
                if (page === "TV PalcoWeb") {
                  return (
                    <S.PagePw
                      style={{ fontSizing: "15px" }}
                      key={String(page)}
                      select={Number(index) === Number(select)}
                      onClick={() => {
                        history.push("/canalpw");
                      }}
                    >
                      <Link to={"/"}>{page}</Link>
                    </S.PagePw>
                  );
                }
                return (
                  <S.Page
                    key={String(page)}
                    select={Number(index) === Number(select)}
                    onClick={() => {
                       
                      setSelect(index);
                    }}
                  >
                    <Link to={pathFunction(index)}>{page}</Link>
                  </S.Page>
                );
              })}
            </S.Pages>

            <S.IconsPerfil>
              {false ? (
                <FaUserCircle onClick={() => history.push(`/profile`)} />
              ) : (
                <>
                  {userIsAuthenticated() ? (
                    <>
                      {/* <MdShoppingCart onClick={() => history.push("/cart")} /> */}
                      <S.NomeLogado >{userSigned && userSigned.fullName}</S.NomeLogado>
                      <S.Perfil
                        onClick={() => {
                          setperfilButton(!perfilButton);
                          setToggle(false);
                        }}
                      >
                        <S.Foto alt="">
                          {firebase.auth().currentUser?.photoURL == null ? <i className="bi bi-person-fill"></i> : <img  style={{width:'50px',height:'50px',borderRadius:"15px"}} src={firebase.auth().currentUser?.photoURL}></img>}
                         
                        </S.Foto>
                        {perfilButton && (
                          <S.PerfilButton>
                            <S.Button
                              onClick={() => history.push(`/espetaculos/assinante`)}
                            >
                              Assinatura
                            </S.Button>
                            <S.Button
                              onClick={() => history.push(`/espetaculos`)}
                            >
                              Meus Espetáculos
                            </S.Button>
                            <S.Button onClick={() => history.push(`/perfil`)}>
                              Minha conta
                            </S.Button>
                            {/* <S.Button
                              onMouseHover={() => setperfilButton(true)}
                            >
                              <span
                                style={{
                                  textDecorationLine: "none",
                                  color: "white",
                                }}
                            
                                onClick={() => {
                                  history.push("/SaibaMais")
                                }}
                              >
                                Saiba Mais
                              </span>
                            </S.Button> */}
                            <S.Button
                              onClick={() => {
                                signOut();
                                history.push("/");
                              }}
                            >
                              Sair
                            </S.Button>
                          </S.PerfilButton>

                        )}
                      </S.Perfil>
                      {!signature?.isActive && <Button 
                            onClick={() => history.push(`/Assinatura`)}
                            text={"Assinatura"}
                          />
                          }
                    </>
                  ) : (
                    <>
                      {/* <MdShoppingCart onClick={() => history.push("/cart")} /> */}
                      <S.DoisBotons>
                        <Button
                          onClick={() => history.push(`/login`)}
                          text={"Entrar"}
                          color="black"
                          textColor="#FFF"
                        />

                        <Button
                          onClick={() => history.push(`/Assinatura`)}
                          text={"Assine já"}
                        />
                      </S.DoisBotons>
                    </>
                  )}
                </>
              )}
            </S.IconsPerfil>

            <S.Collapse>
              <MdMenu
                className="menu"
                onClick={() => {
                  setToggle(!toggle);
                  setperfilButton(false);
                }}
              />
            </S.Collapse>
          </S.Content>
        </S.Container>
      </>
    );
  } else {
    return (
      <>
        {toggle && <S.ModalMenu>
          
          {userIsAuthenticated() ? (
                pagesToggleLogado.map((page, index) => {
                  return (
                    <S.PageToggle
                      key={String(page)}
                      select={Number(index) === Number(select)}
                      onClick={() => setSelect(index)}
                    >
                      <Link to={pathFunctionLogado(index)}>{page}</Link>
                    </S.PageToggle>
                  );
                })
              ) : (
                pagesToggle.map((page, index) => {
                  return (
                    <S.PageToggle
                      key={String(page)}
                      select={Number(index) === Number(select)}
                      onClick={() => setSelect(index)}
                    >
                      <Link to={pathFunction(index)}>{page}</Link>
                    </S.PageToggle>
                  );
                })
              )
              }
        
        </S.ModalMenu>}
        <S.Container>
          <S.Content>
            <img
              src={logo}
              alt="Logotipo Palcoweb"
              onClick={() => history.push("/")}
            />
            <S.Pages>
              {pages.map((page, index) => {
                return (
                  <S.Page
                    key={String(page)}
                    select={Number(index) === Number(select)}
                    onClick={() => setSelect(index)}
                  >
                    <Link to={pathFunction(index)}>{page}</Link>
                  </S.Page>
                );
              })}
            </S.Pages>

            <S.Icons>
              <MdShoppingCart onClick={() => history.push("/cart")} />
              <S.DoisBotons>
                <Button
                  onClick={() => history.push(`/login`)}
                  text={"Entrar"}
                  color="black"
                  textColor="#FFF"
                />
                <Button
                  onClick={() => history.push(`/Assinatura`)}
                  text={"Assine já"}
                />
              </S.DoisBotons>
            </S.Icons>

            <S.Collapse>
              <MdMenu className="menu" 
              onClick={() => {
                setToggle(!toggle);
                setperfilButton(false);
              }} />
            </S.Collapse>
          </S.Content>
        </S.Container>
      </>
    );
  }
}

export default Header;
