import React, { useEffect } from "react";
import { useHistory } from "react-router";
import { SwiperSlide } from "swiper/react";
import { useAuth } from "../../auth/auth";
import ButtonContinuar from "../../components/ButtonContinuar";
import Card from "../../components/Card";
import * as SCard from "../Canais/styles";
import Footer from "../../components/Footer";
import Header from "../../components/Header";
import HorizontalList from "../../components/HorizontalList";
import Section from "../../components/Section";
import { useShows } from "../../dados/context/ShowsContext";
import * as S from "./styles";
import app from "../../auth/config";
import background from '../../assets/logoPalcoweb.png';

function Espetaculos() {
  const {
    showLike,
    showUnLike,
    isInFavorites,
    adquiridos,
    espetaculosFavoritos,
    getShowsByUser,
    getShowsByUserFav,
    getAllChannelsFavoriteByUser,
    channelFavoritesDeep,
  } = useShows();
  const { signature, userIsAuthenticated } = useAuth();
  const userId = userIsAuthenticated() ? app.auth().currentUser.uid : "";
  const history = useHistory();

  const channelEmptyArray = ['', '', '', '', ''];

  useEffect(() => {
    getShowsByUser();
  }, []);

  useEffect(() => {
    getShowsByUserFav();
  }, []);

  useEffect(() => {
    userId && getAllChannelsFavoriteByUser(userId, true);
  }, []);

  console.log("adquiridos", adquiridos);
  // function PaginaAssitir(dia, mes, ano, horas, videoID, videoID2, key) {

  //     history.push(`/playespetaculo/${videoID}/${videoID2}/${key}`)
  //     // window.location.reload()
  // }

  return (
    <>
      <Header />

      <S.Container>
        <h2>Meus Espetáculos</h2>
        <Section title="Adquiridos" filter>
          <HorizontalList id="emAlta">
            {adquiridos?.map((card, i) => (
              <SwiperSlide key={String(card.id)}>
                {card.name === "cardVazio" ? (
                  <Card noContent />
                ) : (
                  <S.CardTest>
                    <S.Img
                      src={card.thumbnail}
                      style={{
                        position: "absolute",
                      }}
                      value={card.id}
                      alt="imagem referente a peça"
                    />

                    <S.InfoCard>
                      <S.InfoCardConteudo>
                        <S.Classificacao>{card.ageRating}</S.Classificacao>

                        <S.Compartilhar>
                          <i className="bi bi-share-fill"></i>
                        </S.Compartilhar>
                      </S.InfoCardConteudo>

                      <S.Titulo>{card.title}</S.Titulo>
                      <S.ConteudoCard
                        style={{
                          textTransform: "uppercase",
                        }}
                      >
                        {/* {show.name} */}
                      </S.ConteudoCard>
                      <S.Sinopse>{card.synopsis}</S.Sinopse>

                      <ButtonContinuar
                        title="Assistir"
                        spectacle={card}
                        toPlan={
                          card.isActiveOnDemand ? "onDemand" : "signature"
                        }
                        userIsSignature={
                          userIsAuthenticated() && signature?.isActive
                            ? true
                            : false
                        }
                        userIsAuth={userIsAuthenticated() ? true : false}
                      />
                    </S.InfoCard>
                  </S.CardTest>
                )}
              </SwiperSlide>
            ))}
          </HorizontalList>
        </Section>
        <Section title="Favoritos" filter>
          <HorizontalList id="emAlta">
            {espetaculosFavoritos?.map((card, i) => (
              <SwiperSlide key={String(i)}>
                {card.name === "cardVazio" ? (
                  <Card noContent />
                ) : (
                  <S.CardTest>
                    <S.Img
                      src={card.thumbnail}
                      style={{
                        position: "absolute",
                      }}
                      value={card.id}
                      alt="imagem referente a peça"
                    />

                    <S.InfoCard>
                      <S.InfoCardConteudo>
                        <S.Classificacao>{card.ageRating}</S.Classificacao>
                        <S.Compartilhar>
                          <i className="bi bi-share-fill"></i>
                        </S.Compartilhar>
                      </S.InfoCardConteudo>

                      <S.Heart
                        isInFavorites={isInFavorites(card.id)}
                        onClick={() => {
                          if (isInFavorites(card.id)) {
                            showUnLike(userId, card.id);
                          } else {
                            showLike(userId, card.id);
                          }
                        }}
                      >
                        <i className="bi bi-suit-heart-fill"></i>
                      </S.Heart>
                      <S.Titulo>{card.title}</S.Titulo>
                      <S.ConteudoCard
                        style={{
                          textTransform: "uppercase",
                        }}
                      >
                        {/* {show.name} */}
                      </S.ConteudoCard>
                      <S.Sinopse>{card.synopsis}</S.Sinopse>

                      <ButtonContinuar
                        title="Assistir"
                        spectacle={card}
                        toPlan={
                          card.isActiveOnDemand ? "onDemand" : "signature"
                        }
                        userIsSignature={
                          userIsAuthenticated() && signature?.isActive
                            ? true
                            : false
                        }
                        userIsAuth={userIsAuthenticated() ? true : false}
                      />
                    </S.InfoCard>
                  </S.CardTest>
                )}
              </SwiperSlide>
            ))}
          </HorizontalList>
        </Section>
        <Section title="Canais Favoritos" filter>
          <HorizontalList id="emAlta">
            {channelFavoritesDeep.length > 0 ?
            channelFavoritesDeep?.map((card, i) => {

              return <>
              <SwiperSlide key={String(i)}>
                {card === "" ? (
                  <Card noContent />
                ) : (
                  <SCard.CardCanais>
                    <SCard.Img
                      src={card.thumbnail}
                      style={{
                        position: "absolute",
                        width: 250,
                        height: 250,
                        borderRadius: "50%",
                        lineHeight: 250,
                        textAlign: "center",
                      }}
                      value={card.key}
                      alt="imagem canal"
                    />
                    <SCard.InfoCard>
                      <SCard.Span
                        size="18"
                        style={{ marginBottom: "15px" }}
                      ></SCard.Span>
                      <SCard.Span size="20">{card.fullName}</SCard.Span>
                      <S.Heart
                        isInFavorites={isInFavorites(card.id)}
                        onClick={() => {
                          if (isInFavorites(card.id)) {
                            showUnLike(userId, card.id);
                          } else {
                            showLike(userId, card.id);
                          }
                        }}
                      >
                        <i className="bi bi-suit-heart-fill"></i>
                      </S.Heart>
                      <SCard.Span size="10">
                        <i className="bi bi-circle-fill"></i>{" "}
                        <SCard.Span size="15">{card.categoria}</SCard.Span>{" "}
                      </SCard.Span>
                      <SCard.Span></SCard.Span>
                      <SCard.verMais
                        size="18"
                        style={{ marginTop: "15px" }}
                        onClick={() => {
                          history.push(`/FichaCanal/${card.id}`);
                          // window.location.reload();
                        }}
                      >
                        Ver mais
                      </SCard.verMais>
                    </SCard.InfoCard>
                  </SCard.CardCanais>
                )}
              </SwiperSlide>
              </>
            })
            :
            channelEmptyArray.map((item, i) => {
               
              return <SwiperSlide key={String(i)}>
              <S.CardEmptyChannel>
                <img src={background} alt=''/>
              </S.CardEmptyChannel>
              </SwiperSlide>
            })
            }
          </HorizontalList>
        </Section>
      </S.Container>
      <Footer />
    </>
  );
}

export default Espetaculos;
