import React from "react";
import * as S from "./styles";
import app from "../../auth/config";
import { useHistory } from "react-router-dom";
import "./style.css";
import Layout from "../../layout";
import Img from "../../assets/imagemAssinatura.png";
import { usePlans } from "../../dados/context/PlansContext";
import { usePayments } from "../../dados/context/PaymentContext";
import { useAuth } from "../../auth/auth";
import { useCarts } from "../../dados/context/CartContext";

function Assinatura() {
  const history = useHistory();
  const { plans } = usePlans();
  const {addToCart} = useCarts();
  const { updateSignature, userIsAuthenticated, signature, setRouteToStack } =
    useAuth();
  const { payFree } = usePayments();
  const userId = userIsAuthenticated() ? app.auth().currentUser.uid : "";
  const isSignature = userIsAuthenticated() && signature?.isActive;
  const planFree = plans.find((plano) => plano.is_test_period === true);

  return (
    <Layout>
      {isSignature ? (
        history.push("/espetaculos/assinante")
      ) : (
        <S.Wrapper>
          <S.Container>
            <>
              <S.Header>
                <S.Div>
                  <span>Teste grátis por 7 dias</span>
                  <br />
                  <br />
                  <S.HeaderSpan>
                    Aproveite desconto em lançamentos e o nosso catálogo com
                    conteúdo exclusivo e ilimitado em peças teatrais, stand-ups,
                    shows e palestras.
                  </S.HeaderSpan>
                  <br />
                  <br />
                  <S.ButtonPagar
                    onClick={() => {
                      payFree(
                        { user: userId, plan_id: planFree.id },
                        (error) => {
                          if (!error) {
                            updateSignature(true);
                            alert("Pagamento feito com sucesso");
                            history.push(`/espetaculos/assinante`);
                          } else {
                            //Tratar Erro no cb
                            // console.log(error);
                            alert(error.message);
                          }
                        }
                      );
                    }}
                  >
                    INICIAR PERÍODO DE TESTE
                  </S.ButtonPagar>
                </S.Div>
                <S.Div>
                  <img className="img" alt="" xsrc={Img}></img>
                </S.Div>
              </S.Header>

              <S.HeaderSpan>Escolha o melhor plano para você:</S.HeaderSpan>
              <br />

              <S.CartActions>
                <S.ButtonVoltar onClick={() => history.push(`/`)}>
                  <i className="bi bi-chevron-left"></i>Voltar{" "}
                </S.ButtonVoltar>
                {plans?.map((plano) => {
                  //reordena do menor para o maior
                  plans.sort(function(a, b){return a.value - b.value})
                  return (
                    <S.ButtonPagar
                      key={plano.id}
                      style={{height:'38vh'}}
                      onClick={() => {
                        if(!isSignature){
                          addToCart({
                            ...plano,
                            signature_id: plano.id,
                            signature_value: plano.value,
                          });
                          setRouteToStack("/Assinatura");
                          history.push("/cart");
                        }else{
                          history.push("/espetaculos/assinante");
                        }
                      }}
                    >
                      <div className="plano">{plano.name}</div> <br />
                      <div className="valorPlano">
                        {/* inclui o valor riscado acima do preço do plano */}
                        {plano.name === 'Mensal' ? 
                        <s style={{textDecoration:'line-through'}} className='texto'>R$29,96</s> 
                        : 
                        plano.name === 'Trimestral' ? 
                        <s style={{textDecoration:'line-through'}} className='texto'>R$79,91</s> 
                        : 
                        plano.name === 'Anual' ? 
                        <s style={{textDecoration:'line-through'}} className='texto'>R$297,52</s> : <div></div>}

                        <div className="valorContainer">
                          <div className="valorPequeno">
                            {plano?.breakValue?.currency}
                          </div>
                          {plano?.breakValue?.integer}
                          <div className="valorPequeno">
                            ,{plano?.breakValue?.decimal}
                          </div>
                        </div>
                        {/* inclui o texto abaixo do preço do plano */}
                        {plano.name === 'Mensal' ? 
                        <s className='texto' style={{color:'transparent'}}>.</s> 
                        : 
                        plano.name === 'Trimestral' || plano.name === 'Anual' ? 
                        <s className='texto' >+ 7 dias grátis</s> : <div></div>}

                      </div>{" "}
                      ASSINAR
                    </S.ButtonPagar>
                  );
                })}
              </S.CartActions>
            </>
            {/* <S.CartInfo>
              <span>
                Torne-se um assinante e garanta 50% de desconto neste conteúdo
              </span>
              <S.CartInfoText>
                E ainda tenha acesso a um amplo conteúdo em peças teatrais,
                shows musicais e palestras para assistir a qualquer hora(exceto
                lançamentos).
              </S.CartInfoText>
            </S.CartInfo> */}
          </S.Container>
        </S.Wrapper>
      )}
    </Layout>
  );
}

export default Assinatura;
