import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { SwiperSlide } from "swiper/react";
import { useAuth } from "../../auth/auth";
import app from "../../auth/config";
import Banner from "../../components/Banner";
import Card from "../../components/Card";
import Share from "../../components/CardCompartilhar/index";
import HorizontalList from "../../components/HorizontalList";
import Auth from "../../components/popups/Auth";
import Info from "../../components/popups/Info";
import RegisterPopup from "../../components/popups/Register";
import Section from "../../components/Section";
import { useShows } from "../../dados/context/ShowsContext";
import Layout from "../../layout";
import * as SCard from "../Canais/styles";
import TelaCardezao from "../Cardezao/index";
import * as S from "./styles";

function Home() {
  const history = useHistory();
  const [visible, setVisible] = useState({ status: false, text: "" });

  const changeVisible = (value) => {
    if (value === "off") {
      setVisible({
        status: false,
        text: value,
      });
    } else {
      setVisible({
        status: true,
        text: value,
      });
    }
  };

  const changeToShow = () => {
    if (visible.text === "Enter") {
      return <Auth visible={visible} changeVisible={changeVisible} />;
    }
    if (visible.text === "Register") {
      return <RegisterPopup visible={visible} changeVisible={changeVisible} />;
    }
    if (visible.text === "Info") {
      return <Info visible={visible} changeVisible={changeVisible} />;
    }
  };

  const {
    showLike,
    showUnLike,
    lancamentos,
    emAltaList,
    isInFavorites,
    getAllChannels,
    channels,
    channelIsInFavorites,
    channelLike,
    channelUnLike,
    getShowsByUserFav,
    isInFavoritesChannel,
    showsSignatures,
    getAllChannelsFavoriteByUser
  } = useShows();
  const { userIsAuthenticated } = useAuth();
  const [abriu, setAbri] = useState(false);
  const userId = userIsAuthenticated() ? app.auth().currentUser?.uid : "";
  const [imgUrl, setImgUrl] = useState("");
  const [link, setLink] = useState("");
  const [cardTitle, setcardTitle] = useState("");
  const [sinopsia, setsinopsia] = useState("");
  const [artista, setartista] = useState("");
  const [categoria, setcategoria] = useState("");
  const [classificacao, setclassificacao] = useState("");
  const [descricaoClassifica, setdescricaoClassifica] = useState("");
  const [elenco, setelenco] = useState("");
  const [producao, setproducao] = useState("");
  const [direcao, setdirecao] = useState("");
  const [duracao, setduracao] = useState("");
  const [compartilhar, setcompartilhar] = useState(false);
  const [videoID, setvideoID] = useState("");
  const [videoID2, setvideoID2] = useState("");

  useEffect(() => {
    getAllChannels();
  }, []);

  useEffect(() => {
    getShowsByUserFav();
  }, []);

  useEffect(() => {
    userId && getAllChannelsFavoriteByUser(userId, false);
  }, []);

  function showConteudo(
    url,
    link,
    title,
    sinopsia,
    artista,
    categoria,
    classificacao,
    descricaoClassifica,
    elenco,
    producao,
    direcao,
    duracao,
    videoID,
    videoID2
  ) {
    if (abriu) {
      setAbri(false);
      setImgUrl("");
      setLink("");
      setcardTitle("");
    } else {
      setAbri(true);
      setImgUrl(url);
      setLink(link);
      setcardTitle(title);
      setsinopsia(sinopsia);
      setartista(artista);
      setcategoria(categoria);
      setclassificacao(classificacao);
      setdescricaoClassifica(descricaoClassifica);
      setelenco(elenco);
      setproducao(producao);
      setdirecao(direcao);
      setduracao(duracao);
      setvideoID(videoID);
      setvideoID2(videoID2);
    }
  }

  return (
    <Layout page={0}>
      <>
        {visible.status ? (
          <>{changeToShow(visible.text)}</>
        ) : (
          <>
            {abriu ? (
              <TelaCardezao
                link={link}
                direcao={direcao}
                producao={producao}
                elenco={elenco}
                voltar={showConteudo}
                artista={artista}
                sinopsia={sinopsia}
                descricaoClassifica={descricaoClassifica}
                classificacao={classificacao}
                img={imgUrl}
                titulo={cardTitle}
                categoria={categoria}
                duracao={duracao}
                videoID={videoID}
                videoID2={videoID2}
              ></TelaCardezao>
            ) : (
              <>
                <Banner />
                <S.Container>
                  <Section title="Lançamentos" filter>
                    <HorizontalList id="emAlta">
                      {lancamentos?.map((show) => (
                        <Section key={show.id} title={show.name} filter>
                          <HorizontalList id={show.name}>
                            {show.spectacle &&
                              show.spectacle.length > 0 &&
                              show.spectacle.map((card) => (
                                <SwiperSlide key={String(card.id)}>
                                  {card === "" ? (
                                    <Card noContent />
                                  ) : (
                                    <S.CardTest>
                                      <S.Img
                                        src={card.thumbnail}
                                        style={{
                                          position: "absolute",
                                        }}
                                        value={card.id}
                                        alt="imagem referente a peça"
                                      />

                                      <S.InfoCard>
                                        <S.InfoCardConteudo>
                                          <S.Classificacao>
                                            {card.ageRating}
                                          </S.Classificacao>
                                          {userIsAuthenticated() && (
                                            <S.Coracao
                                              isInFavorites={isInFavorites(
                                                card.id
                                              )}
                                              onClick={() => {
                                                if (isInFavorites(card.id)) {
                                                  showUnLike(userId, card.id);
                                                } else {
                                                  showLike(userId, card.id);
                                                }
                                              }}
                                            >
                                              <i className="bi bi-suit-heart-fill"></i>
                                            </S.Coracao>
                                          )}

                                          <S.Compartilhar>
                                            <i
                                              className="bi bi-share-fill"
                                              onMouseEnter={() =>
                                                setcompartilhar(true)
                                              }
                                            ></i>
                                            {compartilhar && (
                                              <Share
                                                sair={setcompartilhar}
                                                link={`https://www.palcoweb.com.br/#/cardezao/${card.id}`}
                                              ></Share>
                                            )}
                                          </S.Compartilhar>
                                        </S.InfoCardConteudo>

                                        <S.Titulo>{card.title}</S.Titulo>
                                        <S.ConteudoCard
                                          style={{
                                            textTransform: "uppercase",
                                          }}
                                        >
                                          {show.name}
                                        </S.ConteudoCard>
                                        <S.Sinopsia>{card.synopsis}</S.Sinopsia>

                                        <S.ButtonAcessar
                                          className="btn btn-warning"
                                          onClick={() => {
                                            history.push(`cardezao/${card.id}`);
                                            // window.location.reload();
                                          }}
                                        >
                                          Saiba Mais
                                        </S.ButtonAcessar>
                                      </S.InfoCard>
                                    </S.CardTest>
                                  )}
                                </SwiperSlide>
                              ))}
                          </HorizontalList>
                        </Section>
                      ))}
                    </HorizontalList>
                  </Section>

                  <Section title={"Disponível em Assinatura"}>
                    <HorizontalList>
                      {showsSignatures.spectacle?.map((card, index) => {
                        return (
                          <SwiperSlide key={String(index)}>
                            {card.isActiveOnDemand === false && (
                              <S.CardTest>
                                <S.Img
                                  src={card.thumbnail}
                                  style={{
                                    position: "absolute",
                                  }}
                                  value={card.id}
                                  alt="imagem referente a peça"
                                />

                                <S.InfoCard>
                                  <S.InfoCardConteudo>
                                    <S.Classificacao>
                                      {card.classificacao}
                                    </S.Classificacao>
                                    <S.Coracao>
                                      <i className="bi bi-suit-heart-fill"></i>
                                    </S.Coracao>

                                    <S.Compartilhar>
                                      <i
                                        className="bi bi-share-fill"
                                        onMouseEnter={() =>
                                          setcompartilhar(true)
                                        }
                                      ></i>
                                      {compartilhar && (
                                        <Share
                                          sair={setcompartilhar}
                                          link={`https://www.palcoweb.com.br/#/cardezao/${card.key}`}
                                        ></Share>
                                      )}
                                    </S.Compartilhar>
                                  </S.InfoCardConteudo>

                                  <S.Titulo>{card.title}</S.Titulo>
                                  <S.ConteudoCard
                                    style={{
                                      textTransform: "uppercase",
                                    }}
                                  >
                                    {card.categoria}
                                  </S.ConteudoCard>
                                  <S.Sinopsia>{card.synopsis}</S.Sinopsia>

                                  <S.ButtonAcessar
                                    className="btn btn-warning"
                                    onClick={() => {
                                      history.push(`../cardezao/${card.id}`);
                                    }}
                                  >
                                    Saiba Mais{" "}
                                  </S.ButtonAcessar>
                                </S.InfoCard>
                              </S.CardTest>
                            )}
                          </SwiperSlide>
                        );
                      })}
                    </HorizontalList>
                  </Section>

                  {/* <Section title="Em Alta" filter>
                    <HorizontalList id="emAlta">
                      {emAltaList?.map((show) => (
                        <Section key={show.id} title={show.name} filter>
                          <HorizontalList id={show.name}>
                            {show.spectacle &&
                              show.spectacle.length > 0 &&
                              show.spectacle.map((card) => (
                                <SwiperSlide key={String(card.id)}>
                                  {card === "" ? (
                                    <Card noContent />
                                  ) : (
                                    <S.CardTest>
                                      <S.Img
                                        src={card.thumbnail}
                                        style={{
                                          position: "absolute",
                                        }}
                                        value={card.id}
                                        alt="imagem referente a peça"
                                      />

                                      <S.InfoCard>
                                        <S.InfoCardConteudo>
                                          <S.Classificacao>
                                            {card.ageRating}
                                          </S.Classificacao>
                                          {userIsAuthenticated() && (
                                            <S.Coracao
                                              isInFavorites={isInFavorites(
                                                card.id
                                              )}
                                              onClick={() => {
                                                if (isInFavorites(card.id)) {
                                                  showUnLike(userId, card.id)
                                                } else {
                                                  showLike(userId, card.id);
                                                }
                                              }}
                                            >
                                              <i className="bi bi-suit-heart-fill"></i>
                                            </S.Coracao>
                                          )}

                                          <S.Compartilhar>
                                            <i
                                              className="bi bi-share-fill"
                                              onMouseEnter={() =>
                                                setcompartilhar(true)
                                              }
                                            ></i>
                                            {compartilhar && (
                                              <Share
                                                sair={setcompartilhar}
                                                link={`https://www.palcoweb.com.br/#/cardezao/${card.id}`}
                                              ></Share>
                                            )}
                                          </S.Compartilhar>
                                        </S.InfoCardConteudo>

                                        <S.Titulo>{card.title}</S.Titulo>
                                        <S.ConteudoCard
                                          style={{
                                            textTransform: "uppercase",
                                          }}
                                        >
                                          {show.name}
                                        </S.ConteudoCard>
                                        <S.Sinopsia>{card.synopsis}</S.Sinopsia>

                                        <S.ButtonAcessar
                                          className="btn btn-warning"
                                          onClick={() => {
                                            history.push(`cardezao/${card.id}`);
                                            // window.location.reload();
                                          }}
                                        >
                                          Saiba Mais
                                        </S.ButtonAcessar>
                                      </S.InfoCard>
                                    </S.CardTest>
                                  )}
                                </SwiperSlide>
                              ))}
                          </HorizontalList>
                        </Section>
                      ))}
                    </HorizontalList>
                  </Section> */}
                  {/* <Section title="Em alta" filter>
                  <HorizontalList id="recomendados">
                    {card.map((card, index) => {
                      return (
                        <SwiperSlide key={String(index)}>
                          {card === "" ? (
                            <Card noContent />
                          ) : (
                            <Card index={index} info={card} />
                          )}
                        </SwiperSlide>
                      );
                    })}
                  </HorizontalList>
                </Section> */}

                  <Section title="Canais" filter>
                    <HorizontalList id="canais">
                      {channels?.map((card, index) => {
                        channels.sort((card) => {
                          if (card.fullName === "Tv PalcoWeb") {
                            return -1
                          }
                          return 0;
                        });
                        return (
                          <SwiperSlide key={String(index)}>
                            {card === "" ? (
                              <Card noContent />
                            ) : (
                              <SCard.CardCanais>
                                <SCard.Img
                                  src={card.thumbnail}
                                  style={{
                                    position: "absolute",
                                    width: 250,
                                    height: 250,
                                    borderRadius: "50%",
                                    lineHeight: 250,
                                    textAlign: "center",
                                  }}
                                  value={card.key}
                                  alt="imagem canal"
                                />
                                <SCard.InfoCard>
                                  <SCard.Span
                                    size="18"
                                    style={{ marginBottom: "15px" }}
                                  >
                                    {userIsAuthenticated() && (
                                      <SCard.Coracao
                                        channelIsInFavorites={isInFavoritesChannel(card.id)}
                                        onClick={() => {
                                          channelLike(userId, card.id)
                                        }}
                                      >
                                        <i className="bi bi-suit-heart"></i>
                                      </SCard.Coracao>
                                    )}
                                  </SCard.Span>
                                  <SCard.Span size="20">
                                    {card.fullName}
                                  </SCard.Span>

                                  <SCard.Span size="10">
                                    <i className="bi bi-circle-fill"></i>{" "}
                                    <SCard.Span size="15">
                                      {card.categoria}
                                    </SCard.Span>{" "}
                                  </SCard.Span>
                                  <SCard.Span></SCard.Span>
                                  <SCard.verMais
                                    size="18"
                                    style={{ marginTop: "15px" }}
                                    onClick={() => {
                                      history.push(`/canalpw`);
                                      // window.location.reload();
                                    }}
                                  >
                                    Ver mais
                                  </SCard.verMais>
                                </SCard.InfoCard>
                              </SCard.CardCanais>
                            )}
                          </SwiperSlide>
                        );
                      })}
                    </HorizontalList>
                  </Section>
                </S.Container>
              </>
            )}
          </>
        )}
      </>
    </Layout>
  );
}

export default Home;
