import React, { useEffect } from "react";
import { useHistory, useParams } from "react-router-dom";
import Footer from "../../components/Footer";
import Header from "../../components/Header";
import { useShows } from "../../dados/context/ShowsContext";
import { usePlans } from "../../dados/context/PlansContext";
import * as S from "./styles";
import logoPW from "../../assets/logo-pw.png";


function ConfirmCompra() {
  const history = useHistory();

  const { adquiridos, getShowsByUser } = useShows();
  const { getPlan, plans } = usePlans();
  const routeStack = localStorage.getItem('stack:route');
  const { id } = useParams();
  useEffect(() => {
    getShowsByUser();
  }, []);
  useEffect(() => {
    getPlan();
  }, []);
  //fazer um find e verificar na rota de filmes adquiridos se esse espetáculo está lá
  const espetaculoUsuario = adquiridos.find(
    (show) => show.id === id
  );
  const planoUsuario = plans.find(
    (e) => e.id === id
  );
  if(routeStack!='/pagamento'){
    history.push(routeStack)
  }

  function dataAtualFormatada(){
    var data = new Date(),
        dia  = data.getDate().toString(),
        diaF = (dia.length == 1) ? '0'+dia : dia,
        mes  = (data.getMonth()+1).toString(), //+1 pois no getMonth Janeiro começa com zero.
        mesF = (mes.length == 1) ? '0'+mes : mes,
        anoF = data.getFullYear();
    return diaF+"/"+mesF+"/"+anoF;
}

  return (
    <>
      <Header />

      <S.Container>
          <S.Span size="1.3" className="p-5">
            Agradecemos sua compra
          </S.Span>
          {espetaculoUsuario ? 
            <S.Span className="p-2" cor="#E4B810">
              Você tem 24h para assistir esse espetáculo.
            </S.Span>
            : (<div></div>)
          } 
          
          <div style={{display: "flex", flexDirection: "column", alignItems:'center'}}>
        {espetaculoUsuario ? (
          <>
            
              <S.Img src={espetaculoUsuario.thumbnail}></S.Img>
           

            <S.Span className="pt-2" cor="#E4B810">
              {espetaculoUsuario.price_on_demand}
            </S.Span>
            <S.Span className="pt-2" cor="#E4B810">
              Data da compra: {dataAtualFormatada()}
            </S.Span>

            <S.Span cor="#E4B810">
              Total R$: {espetaculoUsuario.price_on_demand}
            </S.Span>
          </>
        ) :
        planoUsuario ?
        (
          <>
            {<S.ImgPlanos src={logoPW}></S.ImgPlanos>}
           
            <S.Span className="pt-2" cor="#E4B810">
              Plano {planoUsuario.name}
            </S.Span>
            <S.Span className="pt-2" cor="#E4B810">
              Data da compra: {dataAtualFormatada()}
            </S.Span>
           
            <S.Span cor="#E4B810">
              Total R$: {planoUsuario.value}
            </S.Span>
          </>
        ) :
        (
          <div></div>
        )
        }
        </div>

        <S.AreaButtons>
          <S.Button className="btn" onClick={() => history.push(`/`)}>
            <S.Span size="1.0" cor="#FFF">
              {" "}
              <i class="bi bi-chevron-left"></i>Voltar
            </S.Span>
          </S.Button>
          <S.ButtonContinuar
            className="btn"
            onClick={() => history.push(`/espetaculos`)}
          >
            Meus espetáculos
          </S.ButtonContinuar>
        </S.AreaButtons>
      </S.Container>
      <Footer />
    </>
  );
}

export default ConfirmCompra;
